<template>
	<view class="container">
		<view class="background">
		</view>


		<view class="content">
			<u-gap height="10"></u-gap>
			<uni-search-bar style="width: 90%;margin: auto;" color="#FFFFFF" radius="10" placeholder="请输入项目名称或负责人姓名"
				v-model="searchText"></uni-search-bar>
			<u-gap height="12"></u-gap>
			<view class="list" v-for="(item,index) in page.list" :key="index">
				<view class="item" @click="turnTo(item.projectId)">
					<image class="img" mode="widthFix"
						src="@/static/uploads/images/897f2162-94f7-43a3-b68e-7f7ddfce67fc.jpg">
					</image>
					<view class="textBar">
						<view class="title">
							{{ item.title }}
						</view>
						<view class="leader">
							负责人：{{ item.leaderName }}
						</view>
					</view>
				</view>
				<u-gap height="20"></u-gap>
			</view>
		</view>
		<uni-load-more :status="status"></uni-load-more>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				// 搜索框输入值
				searchText: '',
				// 分页查询获取的后端结果
				page: {},
				list: [
					// {
					// 	projectId: 1,
					// 	coverImage: '',
					// 	title: '谈杏花楼故事，养老只好精神',
					// 	leaderName: '刘奇铎',
					// },
					// {
					// 	projectId: 2,
					// 	coverImage: '',
					// 	title: '谈杏花楼故事，养老只好精神',
					// 	leaderName: '刘奇铎',
					// },
					// {
					// 	projectId: 3,
					// 	coverImage: '',
					// 	title: '谈杏花楼故事，养老只好精神',
					// 	leaderName: '刘奇铎',
					// },
				],
				pageNo: 1,
				pageSize: 5,
				status: 'more',
			}
		},
		onLoad() {
			uni.startPullDownRefresh();
		},
		onReachBottom() {
			if (this.pageNo < this.page.pages) {
				this.status = 'loading'; //加载中状态
				this.pageNo++;
				this.getData()
			} else {
				this.status = 'noMore'; //加载完状态
			}
		},
		onPullDownRefresh() {
			this.page = {};
			this.pageNo = 1;
			//调用获取数据方法
			this.getData();
			setTimeout(() => {
				//结束下拉刷新
				uni.stopPullDownRefresh();
			}, 1000);
		},
		methods: {
			getData() {
				const path = '/news/shows';
				uni.request({
					url: `${getApp().globalData.URL}${path}?pageNo=${this.pageNo}&pageSize=${this.pageSize}`,
					method: 'GET',
					header: {
						'token': uni.getStorageSync('token'),
					},
					success: (res) => {
						console.log(res);
						if (this.pageNo === 1) {
							this.page = res.data.data;
						} else {
							this.page.list.push(...res.data.data.list);
						}
					},
					fail: (error) => {
						console.log(error);
					}
				})
			},
			turnTo(id) {
				console.log(id);
				uni.navigateTo({
					url: `/pages/newsDetail/newsDetail?id=${id}`,
				})
			},
		}
	}
</script>

<style lang="less" scoped>
	.background {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: -1;
		background-image: linear-gradient(to bottom, #ffe0dd, #fdecdc, #fcf9da);
	}

	.list {
		width: 90%;
		margin: auto;

		.item {
			border-radius: 10px;
			background-color: white;
			width: 100%;
			overflow: hidden;
			position: relative;

			.img {
				width: 100%;
				display: block;
			}

			.textBar {
				position: absolute;
				bottom: 0;
				height: 50px;
				background-color: rgba(50, 50, 50, 0.4);
				display: flex;
				flex-direction: row;
				justify-content: space-between;
				width: 100%;

				.title {
					margin-left: 10px;
					line-height: 40px;
					font-size: 18px;
					font-weight: 400;
					color: white;
				}

				.leader {
					line-height: 70px;
					font-size: 10px;
					color: grey;
					margin-right: 10px;
					color: white;
				}
			}
		}

	}
</style>